@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;800&display=swap);
:root{--clr-primary: #f3f2ee;--clr-text-primary: #111;--clr-text-secondary: #e53637;--clr-text-muted: #b7b7b7;--sp-xs: 0.25rem;--sp-sm: 0.5rem;--sp-md: 1rem;--sp-lg: 1.5rem;--sp-xl: 2rem;--sp-2xl: 3rem;--sp-3xl: 4rem;--nav-bar-height: 80px;--font-family: "Nunito Sans", sans-serif;--fs-xl: 2rem;--fs-lg: 1.5rem;--fs-md: 1.125rem;--fs-sm: 1rem;--fs-xs: 0.875rem;--fw-md: 600;--fw-bold: 800}*,::before,::after{box-sizing:border-box}html{scroll-behavior:smooth}body{margin:0;padding:0;font-family:"Nunito Sans", sans-serif;font-family:var(--font-family);font-size:1rem;font-size:var(--fs-sm);font-weight:600;font-weight:var(--fw-md);color:#111;color:var(--clr-text-primary)}p,h1,h2,h3,h4,h5{margin:0;padding:0}ul,li{margin:0;padding:0;list-style:none}a{text-decoration:none}a:visited,a :active{color:#111;color:var(--clr-text-primary)}img{display:block;max-width:100%;height:auto}button{outline:none;border:none;color:white;background:transparent;cursor:pointer}#body-click-catcher{position:fixed;top:0;left:0;bottom:0;right:0;background-color:rgba(0,0,0,0.2);z-index:99;display:none}#back-to-top{position:fixed;right:1rem;right:var(--sp-md);bottom:1rem;bottom:var(--sp-md);display:flex;justify-content:center;align-items:center;padding:0.5rem;padding:var(--sp-sm);background-color:#e53637;background-color:var(--clr-text-secondary);border-radius:0.5rem;border-radius:var(--sp-sm);z-index:1000;visibility:hidden}#back-to-top i{font-size:1.5rem;font-size:var(--fs-lg);color:white}#back-to-top.show-scroll{visibility:visible}a.btn{letter-spacing:0.2em;cursor:pointer}a.btn--filled{background-color:#111;background-color:var(--clr-text-primary);color:white;padding:1rem 1.5rem;padding:var(--sp-md) var(--sp-lg);transition:all 0.2s}a.btn--filled:hover{color:#e53637;color:var(--clr-text-secondary)}a.btn--link{position:relative}a.btn--link::after{content:"";position:absolute;bottom:-3px;left:0;height:2px;width:100%;background-color:#111;background-color:var(--clr-text-primary);transition:all 0.3s}a.btn--link:hover::after{width:30%;background-color:#e53637;background-color:var(--clr-text-secondary)}.container--md{max-width:80rem;margin-right:auto;margin-left:auto}section{padding-top:4rem;padding-top:var(--sp-3xl)}section:target{padding-top:calc(4rem + 2rem);padding-top:calc(var(--sp-3xl) + 2rem)}section .section-title{text-align:center;font-size:1.5rem;font-size:var(--fs-lg);margin-bottom:1rem;margin-bottom:var(--sp-md)}

.topbar{position:relative;text-align:center;padding:var(--sp-sm);background-color:var(--clr-text-primary);color:white}.topbar #topbar__close{position:absolute;top:var(--sp-sm);right:var(--sp-sm);font-size:var(--fs-lg);cursor:pointer}.topbar.hide{display:none}.navbar{position:relative;box-shadow:0 2px 4px 0 rgba(0,0,0,0.2)}.navbar.sticky{position:fixed;top:0;left:0;right:0;background-color:white;z-index:100}.navbar__mobile{background-color:white;display:flex;justify-content:space-between;align-content:center;padding:var(--sp-sm)}.navbar__mobile #toggle-menu{font-size:var(--fs-lg);cursor:pointer}@media screen and (min-width: 768px){.navbar__mobile{display:none}}.navbar__logo img{max-height:28px}.navbar__content{position:fixed;left:-100%;top:0;height:100vh;width:80vw;display:grid;text-align:center;justify-content:center;padding:var(--sp-lg);background-color:white;z-index:100;transition:all 0.5s ease-in-out}.navbar__content.show{left:0}@media screen and (min-width: 768px){.navbar__content{position:relative;left:0;grid-template-columns:-webkit-max-content 1fr -webkit-max-content;grid-template-columns:max-content 1fr max-content;align-content:center;width:100%;height:var(--nav-bar-height)}}.navbar__links{align-self:center}@media screen and (min-width: 768px){.navbar__links{display:flex;justify-content:center}}.navbar__links li{font-size:var(--fs-lg);margin-bottom:var(--sp-sm)}.navbar__links li a{transition:all 0.1s}.navbar__links li a:hover{color:var(--clr-text-secondary)}@media screen and (min-width: 768px){.navbar__links li{font-size:var(--fs-md)}.navbar__links li:not(:last-child){margin-right:var(--sp-md)}}.navbar__icons{align-self:end}.navbar__icons a{font-size:var(--fs-lg)}.navbar__icons a:not(:last-child){margin-right:var(--sp-md)}

@-webkit-keyframes fade-in-right{0%{opacity:0;-webkit-transform:translateX(-50px);transform:translateX(-50px)}100%{opacity:1;-webkit-transform:translateX(0);transform:translateX(0)}}@keyframes fade-in-right{0%{opacity:0;-webkit-transform:translateX(-50px);transform:translateX(-50px)}100%{opacity:1;-webkit-transform:translateX(0);transform:translateX(0)}}@-webkit-keyframes fade-in-bottom{0%{opacity:0;-webkit-transform:translateY(50px);transform:translateY(50px)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes fade-in-bottom{0%{opacity:0;-webkit-transform:translateY(50px);transform:translateY(50px)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}

.best-sellers{padding:var(--sp-md)}.best-sellers__grid{display:grid;grid-column-gap:var(--sp-md);-webkit-column-gap:var(--sp-md);column-gap:var(--sp-md);grid-row-gap:var(--sp-xl);row-gap:var(--sp-xl);grid-template-columns:repeat(2, 1fr)}@media screen and (min-width: 1024px){.best-sellers__grid{grid-template-columns:repeat(4, 1fr)}}.best-sellers__grid .product{position:relative;overflow:hidden}.best-sellers__grid .product:hover .product__actions{right:15px;opacity:1}.best-sellers__grid .product__actions{position:absolute;right:-150px;top:15px;display:flex;flex-direction:column;opacity:0;transition:0.3s}.best-sellers__grid .product__actions i{cursor:pointer;background-color:white;padding:var(--sp-sm);font-size:var(--fs-md);margin-top:var(--fs-xs)}.best-sellers__grid .product__img{min-width:100%}.best-sellers__grid .product:hover .product__title{opacity:0}.best-sellers__grid .product__title{margin-top:var(--sp-sm);transition:all 0.3s}.best-sellers__grid .product__rating i{color:var(--clr-text-muted);margin:var(--sp-xs) 0}.best-sellers__grid .product__price{font-size:var(--fs-md)}.best-sellers__grid .product__price--before{font-size:var(--fs-sm);margin-right:var(--sp-sm);color:var(--clr-text-muted);text-decoration:line-through}.best-sellers__grid .product:hover .add-to-cart{opacity:1;visibility:visible;top:0}.best-sellers__grid .product__info{position:relative}.best-sellers__grid .product__info .add-to-cart{position:absolute;color:var(--clr-text-secondary);font-weight:var(--fw-bold);top:-10px;opacity:0;visibility:hidden;transition:all 0.3s}

.blogs{padding:var(--sp-md)}.blogs__grid{display:grid;grid-column-gap:var(--sp-md);-webkit-column-gap:var(--sp-md);column-gap:var(--sp-md);grid-template-columns:1fr}@media screen and (min-width: 768px){.blogs__grid{grid-template-columns:repeat(2, 1fr)}}@media screen and (min-width: 1024px){.blogs__grid{grid-template-columns:repeat(3, 1fr)}}.blogs__grid .blog__item{position:relative;margin-bottom:150px}.blogs__grid .blog__item__img{overflow:hidden}.blogs__grid .blog__item__img:hover img{-webkit-transform:scale(1.1);transform:scale(1.1)}.blogs__grid .blog__item__img img{width:100%;transition:all 0.3s ease-in-out}.blogs__grid .blog__item__text{position:absolute;padding:var(--sp-lg);margin:0 var(--sp-xl);margin-top:-30px;background-color:white;box-shadow:0 2px 4px 0 rgba(0,0,0,0.2)}.blogs__grid .blog__item__text h4{margin-bottom:var(--sp-sm)}

.collections__grid{display:grid;grid-template-rows:repeat(2, 1fr);grid-row-gap:var(--sp-lg);row-gap:var(--sp-lg);padding:var(--sp-md)}@media screen and (min-width: 768px){.collections__grid{grid-template-columns:repeat(2, 1fr);grid-template-rows:1fr;grid-column-gap:var(--sp-md);-webkit-column-gap:var(--sp-md);column-gap:var(--sp-md)}}.collections__grid .banner{position:relative;overflow:hidden}.collections__grid .banner__img{object-fit:cover;width:100%;transition:all 0.5s ease-in-out}.collections__grid .banner__text{margin:var(--sp-sm)}.collections__grid .banner__text h2{margin-bottom:var(--sp-sm)}@media screen and (min-width: 768px){.collections__grid .banner__text{position:absolute;top:25%;left:var(--sp-md)}}.collections__grid .banner:hover img{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1);-webkit-transform-origin:right;transform-origin:right}

.footer{background-color:var(--clr-text-primary);color:white;padding:var(--sp-xl)}.footer__logo img{margin-bottom:var(--sp-md);max-height:50px}.footer__about{margin-bottom:var(--sp-md)}.footer__grid{display:grid;grid-gap:var(--sp-lg);gap:var(--sp-lg);grid-template-columns:1fr}@media screen and (min-width: 768px){.footer__grid{grid-template-columns:repeat(4, 1fr);justify-content:space-between;padding:var(--sp-lg) 0}}@media screen and (min-width: 1024px){.footer__grid__item{justify-self:center}}.footer__grid__item h6{font-size:var(--fs-md);color:white;margin-top:0;margin-bottom:var(--fs-md)}.footer__grid__item .footer__links li{margin-bottom:var(--sp-sm)}.footer__grid__item .footer__links li a{color:white;transition:all 0.3s}.footer__grid__item .footer__links li a:hover{color:var(--clr-text-secondary)}.footer__grid__item .news-letter{margin-top:var(--sp-md);max-width:300px;display:flex;border-bottom:1px solid white}.footer__grid__item .news-letter input{background-color:transparent;color:white;padding:var(--sp-sm) 0;font-size:var(--fs-sm);outline:none;border:none;width:100%}.footer__grid__item .news-letter button i{font-size:var(--fs-lg)}.footer__bottom{font-size:var(--fs-xs);border-top:1px solid rgba(255,255,255,0.6);margin-top:var(--sp-md);padding-top:var(--sp-md);text-align:center}

section#hero{background-color:var(--clr-primary)}.hero{display:grid;overflow:hidden}.hero__img{-webkit-animation-name:fade-in-bottom;animation-name:fade-in-bottom;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in}@media screen and (min-width: 768px){.hero{grid-template-columns:2fr 3fr}.hero__img{max-height:500px;justify-self:end}}.hero__content{padding:var(--sp-md);-webkit-animation-name:fade-in-right;animation-name:fade-in-right;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in}.hero__content h2{font-size:var(--fs-md);letter-spacing:0.1em;color:var(--clr-text-secondary);margin-bottom:var(--sp-xs)}.hero__content h1{font-size:var(--fs-xl);margin-bottom:var(--sp-md)}.hero__content p{margin-bottom:var(--sp-2xl)}@media screen and (min-width: 768px){.hero__content{align-self:center}}

